:root {
    --primary-color: white;
    --secondary-color: yellow;
    --primary-background: steelblue;
    --secondary-background: #191970	;
    --tertiary-background: white;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    line-height: 1.5em;  
}

* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background: var(--primary-background);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

li, a {
    color: var(--light);
    text-decoration: none;
}

body {
    display: flex;
    flex-direction: column;
}

header#cabecalho-principal {
    background: var(--secondary-background);
    color: var(--primary-color);
    height: 200px;
    display: grid;
    width: 100%;
}

main{
    background-color: whitesmoke;
    margin: 0px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px,1fr));
    width: 100%;
}

main section {
    background-color: whitesmoke;
    height: 200px;
    border: 10px solid navy;
    text-justify: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: x-large;
    font-weight: bold;
    color: navy;
}

nav#navegacao-principal {
    background: var(--secondary-background);
    color: var(--primary-color);
    display: flex;
    justify-content: center;
    grid-area: nav;
}

nav#navegacao-principal ul {
    margin: 0;
    padding: 0;
}

nav#navegacao-principal li {
    display: inline-block;
    padding: 10px;
}

